    import {mapState} from 'vuex';
    
    export default {
        name: 'app-iphone-bottom',
	    props: {
            backgroundColor: {
                type: String,
	            default: function() {
	                return 'white';
	            }
            }
	    },
	    computed: {
            ...mapState('gConfig',{
                iphone: (data) => {
                    return data.iphone;
                },
                iphoneHeight: (state) =>{
                    return  state.iphoneHeight;
                },
            })
	    }
    }
